<template>
 <transition name="el-fade-in-linear">
  <el-row>
    <el-col v-for="(v,i) in GameList" :key="i" :xs="24" :sm="12" :md="8" :lg="8" :xl="1" class="gameLi">
      <div class="gameIntro">
        <div class="logoCon">
          <router-link title='title' :to="{path:'/detail', query:{'id':v.id}}" >
            <img :src="v.versions[0].big_icon_url">
          </router-link>
        </div>
        <div class="intro">
          <div class="introview">
            <h4>
              <router-link title="title" :to="{path:'/detail', query:{'id':v.id}}" >
                <span>{{v.name}}</span>
              </router-link>
              <router-link class="viewBtn" :to="{path:'/detail', query:{'id':v.id}}" >查看</router-link>
            </h4>
          </div>
          <p class="over20hide" ref="text">{{v.descrip[0]}}</p>
        </div>
        <span class="type">{{i>8?(i+1):'0'+(i+1)}}</span>
      </div>
    </el-col>
  </el-row>
</transition>
</template>
<script>
function dealContent (v) {
  if (v.clientHeight > 54) {
    v.innerHTML = v.innerHTML.substring(0, v.innerHTML.length - 10) + '...'
    dealContent(v)
  }
}
export default {
  props: {
    GameList: {
      type: Array,
      default: () => []
    }
  },
  data: function () {
    return {

    }
  },
  watch: {
    GameList: {
      immediate: true,
      handler: function () {
        this.$nextTick(() => {
          console.log(this.GameList)
          this.initConntent()
        })
      }
    }
  },
  methods: {
    initConntent () {
      this.$refs.text.map(v => {
        dealContent(v)
      })
    }
  },
  mounted () {
    this.initConntent()
  }
}
</script>
<style lang="scss" scoped>
.gameLi{
  .gameIntro{
    background: white;
    position: relative;
    height: 152px;
    margin: 20px 0 0 20px;
    box-shadow:#dedddd 0 2px 4px;
    padding: 10px 5px 0 10px;
    .logoCon{
      float: left;
      width: 68px;
      a{
        img{
          border-radius: 10px;
          width: 68px;
        }
        &:hover{
          transition: all 1s;
          transform: scale(1.1);
        }
      }
    }
    .intro{
      padding: 3px;
      margin: 0 0 0 78px;
      .introview{
        height: 100%;
        h4{margin: 10px 0;
          height: 26px;
          line-height: 26px;
          text-align: left;
          font-size: 15px;
          color: #333333;
        }
        a{
          span{
            color: #333333;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            max-width: 100px;
            float: left;
          }
        }
        a.viewBtn{
          float: right;
          width: 64px;
          color: white;
          background: #24aa42;
          text-align: center;
          font-size: 12px;
          height: 26px;
          line-height: 26px;
          border-radius: 4px;
        }
      }
      p{
        text-align: left;
        color: #999;
        font-size: 12px;
        padding-right: 3px;
      }
    }
    .type{
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 12px;
      line-height: 19px;
      text-align: center;
      padding: 3px 12px 0;
      color: #999;
      background: #ebebeb;
    }
  }
}
</style>



