<template>
<keep-alive>
  <div class='clearfix'>
    <div class="gameListCon inner" v-loading = 'loading'>
      <game-list v-if="gameList.length > 0" :GameList ='gameList' />
      <!-- <button class="watchMore" @click="loadData()">查看更多</button> -->
    </div>
  </div>
</keep-alive>
</template>
<script>
import axios from 'axios'
import gameList from '@/components/gameList'
export default {
  components: {
    gameList
  },
  data: function () {
    return {
      gameList: [],
      loading: false
    }
  },
  created () {
    console.log(this.$route.query.type)
    this.loadData()
  },
  methods: {
    loadData () {
      this.loading = true
      axios.get('../../../static/Json/game_list.json').then(res => {
        this.loading = false
        this.gameList = res.data.data
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.clearfix {
  background: #f5f5f5;
  padding: 20px 0;
}
.gameListCon {
  padding: 0 20px 70px 0;
  position: relative;
}
.watchMore {
  margin: 23px 0;
  position: absolute;
  left: 50%;
  margin-left: -48px;
  color: #333333;
  background: #ddd;
  border: none;
  line-height: 35px;
  font-size: 14px;
  width: 96px;
}
</style>



